import axios from "axios";
import  {Toast} from 'antd-mobile';
import  {history}  from "&";
// const baseURL="http://localhost:4321/"; 
// const baseURL="http://47.105.44.9:4321/"; 
const baseURL="https://huyulin.top:4321/"; 

let token=""
// const baseURL="http://47.105.44.9:1234/";   //http://localhost:1234/vue/movie
// axios.defaults.baseURL = 'https://api.example.com';  //
 axios.defaults.baseURL=baseURL;
 axios.defaults.headers.common['token'] =token;   //token AUTH_TOKEN  token
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';//&+ASCII HEX

export  function loadingToast(msg,time) {
    msg=msg||"";
    time=time||1;
    Toast.hide();  //
    Toast.loading(msg, time, () => {
        // console.log(' !!!');
      });
  }

//
 export function fail(msg) {
    Toast.hide();  //
    Toast.fail(msg, 1);
  } 
//
  export function offline(msg) {
    Toast.offline(msg, 1);
  }

//  
export  function success(msg) {
    Toast.success(msg, 1);
  }
  
//   //interceptors  
axios.interceptors.request.use(function (config) {
    // Do something before request is sent 
    // console.log("");
     // 
     loadingToast();
      token=sessionStorage.token?sessionStorage.token:"";
    //  //
      config.headers['token']=token;  
      //  token  token (sessionStorage )    request.headers.token = token  

     return config;
  }, function (error) {
    // Do something with request error  
    offline(",")
    return Promise.reject(error);
  });

//
  axios.interceptors.response.use(function (response) {
    // Do something with response data    
    // console.log("");
    console.log(response);
    if(response.data.code=="3000"){
       //  router.push({name:'login',query:{active:'login'}})
       history.push("/login");
  }
     // type      success
    // type = 0  fail
    // type = 1  success 
    //type
    if(!!response.data.type){
      success(response.data.msg);
    }else{
      if(response.data.type==0){
        fail(response.data.msg);
      }else{
        success(response.data.msg);
      }
      
    }
    return response;
  }, function (error) {
    // Do something with response error   //
    fail(",")
    return Promise.reject(error);
  });

  export {axios,baseURL} //
